import krudtools from "./krudtools.vue"

export default {
    props: ["title", "icon", "grid", "form", "hideHeader", "hasSelection", "actions", "dbClickAction", "onRowSelect", "rowCurrentChange", "permissions", "user_condition", "custom_condition", "view_url", "mode", "onPropertySuccess", "onPropertyError"],
    components: {
        krudtools
    },
    data() {
        return {
            gridSrc: "",
            formSrc: "",
            editMode: false,
            searchModel: '',
            form_width: 600,
            isPrint: false,
            isExcel: false,
            isRefresh: false,
            isSave: false
        };
    },
    computed: {
        hasVNavSlot() {
            return !!this.$slots['v-nav']
        },

        hasNavSlot() {
            return !!this.$slots['nav']
        },

        hasLeftSlot() {
            return !!this.$slots['left']
        }
    },
    methods: {
        view(id) {
            window.open(this.view_url + id, '_blank');
        },

        edit(id) {
            // console.log(id);
            this.editMode = true;
            this.$refs.form.editModel(id);
            //From template
            if (this.templateEdit) {
                this.templateEdit();
            }
        },

        clone(id) {
            this.$refs.form.cloneModel(id);
            if (this.templateEdit) {
                this.templateEdit();
            }
        },

        quickEdit(id) {
            console.log(id);
        },

        refresh() {
            this.searchModel = null;
            this.$refs.grid.refresh();
        },

        search(q) {
            this.$refs.grid.searchData(q, 1);
        },

        exportExcel() {
            this.$refs.grid.exportExcel();
        },

        print() {
            this.$refs.grid.print();
        },

        save() {
            this.$refs.grid.saveGridData();
        },

        //Form functions
        onSuccess(val) {
            console.log("this.mode");
            console.log(this.mode);
            if (typeof this.mode !== 'undefined' && this.mode && this.mode == 'refresh') {
                this.$refs.grid.refresh();
            } else {
                if (this.editMode) {
                    this.$refs.grid.update(val);
                } else {
                    this.$refs.grid.append(val);
                }
            }

            //From template
            if (this.templateOnSuccess) {
                this.templateOnSuccess();
            }

            //From property
            if (this.onPropertySuccess) {
                this.onPropertySuccess();
            }
        },

        onError(val) {
            //From template
            if (this.templateOnError) {
                this.templateOnError();
            }

            //From property
            if (this.onPropertyError) {
                this.onPropertyError();
            }
        }
    },
}
